.popup__close {
  width: 32px;
  height: 32px;
  border: 0;
  padding: 0;
  background: url(../../../image/close.svg);
  position: absolute;
  top: -40px;
  right: -40px;
  background-size: cover;
}

.popup__close:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 690px) {
  .popup__close {
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0px;
  }
}
